@import url(https://fonts.googleapis.com/css2?family=Noto+Serif:wght@400;700&display=swap);
:root {
	--primary: #efb6b2;
	--secondary: #4e4e4e;
	--error: #ff4a4a;
}

/* base styles & title */
body {
	max-width: 960px;
	margin: 0 auto;
	font-family: 'Noto Serif';
	color: #4e4e4e;
	color: var(--secondary);
	background-color: #fcf8f7;
}
.App {
	margin: 0 auto;
}
.title h1 {
	color: #efb6b2;
	color: var(--primary);
	font-size: 1.2rem;
	letter-spacing: 2px;
	font-weight: normal;
}
.title h2,
.title p {
	text-align: center;
}
.title h2 {
	margin-top: 10px;
	font-size: 2.6rem;
	font-family: 'Dancing Script', cursive;
}

