/* modal styles */
.backdrop {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.5);
}
.backdrop img {
	display: block;
	max-width: 60%;
	max-height: 80%;
	margin: 60px auto;
	box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.5);
	border: 3px solid white;
}

.progress-bar {
  height: 5px;
  background: var(--primary);
  margin-top: 20px;
}
form {
	margin: 30px auto 10px;
	text-align: center;
}
label input {
	height: 0;
	width: 0;
	opacity: 0;
}
label {
	display: block;
	width: 40px;
	height: 40px;
	border: 1px solid var(--primary);
	border-radius: 50%;
	margin: 10px auto;
	line-height: 40px;
	color: var(--primary);
	font-weight: bold;
	font-size: 24px;
}
label:hover {
	background: var(--primary);
	color: white;
}
.output {
	height: 30px;
	font-size: 0.8rem;
}
.error {
	color: var(--error);
}

