/* image grid styles */
.img-grid {
	margin: 20px auto;
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	grid-gap: 40px;
}
.img-wrap {
	overflow: hidden;
	height: 0;
	padding: 50% 0;
	/* padding controls height, will always be perfectly square regardless of width */
	position: relative;
	opacity: 0.8;
}
.img-wrap img {
	width: 30%;
	padding-bottom: 30%;
	object-fit: cover;
	position: absolute;
